.card{
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 20vh;
  color: #420a77bd;
}

.card + .card{
  border-left: solid 1px #0000001e;
}

.temp{
  display: flex;
  flex-direction: column;
  width: 3rem;
  justify-content: center;
  text-align: center;

}
.temp + .temp{
  margin-left: 1rem;
}

.temps{
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  font-size: medium;
}



.card>img{
  width: 5rem;
  height: 5rem;
}

.primary{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 5vh 15vh;
  width: 20rem;
  padding: 0 0.5rem;
}
.primary>span{
  margin-top: 0.25rem;
  grid-column: 1 / span 2;
}
.primary>img{
  width: 8rem;
  height: 8rem;
}

.name{
  background-color: #7e7b7b98;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-weight: 700;
  display: flex;
  align-items: center;
}

.button{
  border: solid 2px transparent;
  border-radius: 9999px;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  transition: background-color 0.25s;
  cursor: pointer;
  color: white;
  margin-left: 0.5rem;
  margin-right: -0.5rem;
}
.button:hover{
  background-color: white;
}