.app {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.bkg {
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(13px);
  background-color: #77777770;
}

.container {
  width: 80vw;
  height: 80vh;
  margin: 10vh 10vw;
  top: 0;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50vh 20vh;
}



.app,
.container {
  background-image: url("./asse/ericadan-koln-sam-almanya-3d9b6ef906f12b772d059391c1383e60.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  border-radius: 1.5rem;
}